.banner {
    position: relative;
    margin: 24px 0;
    padding: 20px 30px 20px 115px;
    box-shadow: 0 0 5px rgba(0, 0, 0, .15);
    background: #F1FAEE;
    line-height: 20px;
    font-size: 16px;
    font-weight: 300;
}

a:hover {
    text-decoration: none;
}

.btn {
    font-family: 'Lucida Sans', sans-serif;
    font-weight: 300;
}
.nav-side {
    margin: 55px 0 0;
    min-height: 227px;
}

.home, .navbar-tutorial {
    width: 100%;
    height: 45px;
    line-height: 45px;
    display: inline-block;
    clear:both;
    position: relative;
    font-size: 18px;
    margin: 0, 10em, 0, 0;
    font-family: 'Lucida Sans', sans-serif;
    font-weight: 300;
    color: #E63946;
}

.btn-danger {
    background-color:#E63946;
    border-color:#E63946;
}

.btn-danger:hover {
    background-color:#b10404;
    border-color:#b10404;
}

.side-bar {
    background-color: #1D3557;
    height: 100vh;
    padding: 50px 0 0 50px;
    position: relative;
    opacity: 0.9;
}

.main {
    background-color: #457B9D;
    height: 100vh;
}

.container-fluid {
    overflow: hidden;
}
.list-overflow-container {
  max-height: 65%;
  border: 1px #DDDDDD solid;
  background-color: #F1FAEE;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-group-item {
  background-color: #F1FAEE;
}
.delete-btn {
  background-color: red;
  border-color: red;
  float: right;
  margin-top:12px;
  color:white;
}



.delete-btn {
  float: right;
}

.floatRight {
    display:inline-block;
    padding: 0 5px;

}

.noteIcon:hover {
    cursor:help;
   }
   

.left-articles, .mid-articles, .right-articles {
    opacity: 0.8;
    height:100vh;
}

p {
    font-family: 'Lucida Sans', sans-serif;
    font-size: 1.2em;
    font-weight:300;
}

.article-url:hover {
    -webkit-text-decoration: underline #1D3557;
            text-decoration: underline #1D3557;
    
}

.article-url h4 {
    color: #4b88aa;
}
